
/* 모바일 좌우 스크롤 금지하는 overflow가 있어서 sticky가 동작하지않습니다
그래서 useEffect 함수를 사용하여 해결합니다. 2023.07.04 윤지석 */

/* 모바일 좌우(수평) 스크롤 금지 */
html, body {
  max-width: 100%;
  overflow-x: hidden;
  }

p {
  margin: 0;
  padding: 0;
}

header {
  width: 100vw;
  background: #4D6DED;
  box-sizing: border-box;
  position: relative;
}
header main img {
 max-width: 100%;
 height: auto;
}
main {
  padding-bottom : 50px;
}
nav {
  display: flex;
  justify-content: space-between;
  align-items: center; 
}
nav.side_menu {
  z-index:999; 
  background: #fff;
}

ul.menu_wrap {
  list-style: none;
  font-family: NotoSansKR-Bold;
  font-size: 20px; 
  color: #293F52;
  padding-left: 24px;
}
ul.menu_wrap li{
  margin-bottom: 30px; 
}

.navigation {
  display: flex;
  width: 100vw;
  height: 58px;
  filter: drop-shadow(0px 3px 20px rgba(0, 0, 0, 0.150));
  background: #fff;
  font-family: NotoSansKR-Bold;
  padding-left: 16px;
  box-sizing: border-box;
  text-align: center;
  z-index:990;
}

.navigation.opacity {
  opacity: 0;
}
.navigation .top_menu {
  display: flex;
  align-items: center;
}

.navigation .top_menu span.Active_menu {
  color:#293F52!important;
  font-family: NotoSansKR-Black!important;

}

.navigation .top_menu span{
  color: #DBDBDB;
  text-decoration: none;
} 

#sticker {
  position: fixed;
  top: 0px;
  left: 0px;
}
.sticky {
  position: fixed;
  top: 0px;
  left: 0px;
}

/* 서비스 1 wellpps 솔루션 -------------------------------------*/

.serviceBox {
  width: 100%;
  box-sizing: border-box;
  border-radius: 16px;
  padding: 0 16px; 
  position: relative;
 }

 .serviceBox_1 .service_subtext {
  font-family: NotoSansKR-Light;
 }
 .serviceBox_1 .sub_style {
  margin-bottom: 4px;
 }
 .serviceBox_1 .service_maintext {
  font-family: NotoSansKR-Bold;
 }

 .serviceBox_1 img {
  width: 100%;
  height: auto;
  position: relative;
  top: 4px;
 }

 /* 이미지 슬라이더 */
 .slider_box {
  box-sizing: border-box;
  padding: 0 16px 0 16px;
 }
 .purple {
  background: #EFF0FF;
 }
 .deep_purple {
  background: #DBDEFC;
 }
 .green {
  background: #E8F8E9; 
 }
