  background: #D2EBD3;
 }

 .yellow {
  background: #FDF8E5; 
 }
 .deep_yellow {
  background: #F7ECC4;
 }

 .slider_info .main_text {
  font-family: NotoSansKR-Bold;
 }
 .slider_info .sub_text {
  font-family: NotoSansKR-Light;
  
 }

 /* 주요서비스 1 배경색 */
