 /* 업무방식 >> 우리가 일하는 방식 -------------------------------------*/

 .slider_box2 {
  box-sizing: border-box;
  padding: 24px;
  font-family: GothamBlack;
  font-size: 40px;
 }

 .slider_box2 .slider_child {
  height: 328px;
  border-radius: 16px;
  box-shadow: 6px 3px 32px #00000024;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  box-sizing: border-box;

 }
 .slider_child .workflow_img {
  width: 220px;
  margin: 0 auto;
 }

 .slider_blueBack {
  background-color: #0382FA!important;
 }
 .slider_purpleBack {
  background: #441949;
 }

 .workflow_para {
  font-family: NotoSansKR-Light;
  padding: 0 24px;
  box-sizing: border-box;
  font-size: 14px;
  line-height: 24px;
 }

/* 조직구성 >> 우리의 구성원들 -------------------------------------*/
.team_card {  
  box-sizing: border-box;
  padding: 0 16px;
  margin-bottom: 8px;
}
.team_card_margin {
  margin-top: 28px;
}
.team_wrap {
  width: 100%;
  height: 150px;
  border-radius: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  box-shadow: 6px 3px 32px #00000024;
}

.team_wrap .emoji {
  width: 88px;
  height: 88px;
  position:relative;
}
.team_wrap .emoji img {
  position: absolute;
  width: 60%;
  height: 60%;
  left: 25%;
  top: 15%;
  object-fit: cover;
}
.team_info .team_main {
  font-family: NotoSansKR-Bold!important;
  font-size: 20px;
}
.team_info .team_sub {
  font-family: NotoSansKR-Light!important;
  font-size: 16px;
}

/* 걸어온길 >> 연혁 -------------------------------------*/

#history_wrap {
  width: 100%;
  margin-top: 28px;
}
#history_wrap .year {
  padding: 0 16px;
  box-sizing: border-box;  
  margin-bottom: 40px;
}

.year_text {
  border-bottom: 1px solid #0077ed;
  font-family: NotoSansKR-Bold;
  font-size: 24px;
  color: #0077ed;
  line-height: 50px;
}
.month_wrap {
  margin-top: 16px;
  font-size: 14px;
  font-family: NotoSansKR-Medium;
  display: flex;
  color: #656565;

}

.month_wrap .month {
  font-family: NotoSansKR-Bold;
  font-size: 18px;
  color: #0077ed;
  margin-right: 14px;
  flex-basis: 20%;
}
.month_wrap .sub_text {
  flex-basis: 55%;
  position: relative;
  top: 4px;
}
.month_wrap .month_img{
  padding: 8px;
  flex-basis: 25%;
  position:relative;
}
.month_wrap .month_img img{
  padding: 10px;
  border: 1px solid #D6D6D6;
  border-radius: 8px;
  box-shadow: 6px 4px 12px rgb(0, 0, 0, 0.05);
  position:absolute;
  top: 0;
  right: 0;
}

/* CONTACT - 오시는길 -------------------------------------*/

.tab_wrap {
  margin-top: 28px;
}
.tab_wrap .tab_bar {
  padding: 0 16px;
  box-sizing: border-box;
  display: flex;
  font-size: 16px;
  color: #D6D6D6;
  font-family: NotoSansKR-Bold;
  margin-bottom: 30px;
}

.tab_wrap .tab_bar .active_tab {
  border-bottom: 2px solid #1A1A1A;
  color: #1A1A1A;
  padding-bottom: 4px;
}
.active_margin {
  margin-right: 36px;
}
.address_box {
  width: 100%;
  box-sizing: border-box;
  padding: 0 32px;
}

.address_box .address_wrap {
  height: 300px;
}

.address_list {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
}
.address_list .title {
  font-size: 18px;
  color: #0077ed;
  font-family: NotoSansKR-Bold;
  flex-basis: 40%;
}

.address_list .info {
  font-size: 14px;
  color: #656565;
  font-family: NotoSansKR-Medium;
  flex-basis: 60%; 
}
.address_wrap .map {
  height: 300px;
}

/* CONTACT- 업무제휴 -------------------------------------------*/
.input_box {
  width: 100%;
  box-sizing: border-box;
  padding: 0 16px;
}
.input_box .input_wrap {
  font-size: 14px;
  color: #1A1A1A;
  font-family: NotoSansKR-Medium;
}
.input_list{
  margin-bottom: 30px;
}
.input_list input {
  margin-top: 10px;
  width: 100%;
  height: 42px;
  border: 1px solid #0077ed;
  border-radius: 8px;
  box-sizing: border-box;
  padding-left: 16px;
  color: #0077ed;
  font-family: NotoSansKR-Bold;
  font-size: 16px;
  box-shadow: 6px 4px 12px rgb(0, 0, 0, 0.1);
}
.input_list input::placeholder {
  color: #D6D6D6;
}

.radio-container {
  display: flex;
  align-items: center;
  margin-top: 10px;
}

.radio-container input[type="radio"] {
  display: none;
}

.radio-container label {
  display: inline-block;
  padding: 8px 12px;
  background-color: #fff;
  border-radius: 8px;
  cursor: pointer;
  margin-right: 10px;
  border: 1px solid #D6D6D6;
  box-shadow: 6px 4px 12px rgb(0, 0, 0, 0.1);
  color: #D6D6D6;
  font-family: NotoSansKR-Bold;
  font-size: 16px;
  flex-basis: 50%;
  text-align: center;
}

.radio-container input[type="radio"]:checked + label {
  border: 1px solid #0077ed;
  color: #0077ed;
}

.request_btn {
  height: 54px;
  box-shadow: 6px 3px 32px #00000024;
  border-radius: 8px;
  background-color: #0077ed;
  font-family: NotoSansKR-Black;
  color: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 18px;
  box-shadow: 5px 5px 5px rgb(0, 0, 0, 0.1);
  margin-top: 40px;
}
.request_btn:active{
  position: relative;
  top: 1px;
  right: 1px;
  box-shadow: none;
}

/* footer --------------------------------------------------- */
#footer {
  box-sizing: border-box;
}
.ask_us {
  display: flex;
  justify-content: space-between;
}
.ask_us .ask_child {
  flex-basis: 50%;
  text-align: center;
  font-family: NotoSansKR-Black;
  font-size: 14px;
  color: #fff;
  padding : 24px 0;
  background-color: #202020;
}
.family_site {
  
}
.family_wrap {
  box-sizing: border-box;
  padding: 0 8%;
  background-color: #141414;
}
.site_list {
  display: flex;
  justify-content: space-between;
  color: #808080;
  font-family: NotoSansKR-Medium;
  font-size: 16px;
  padding: 16px;
  box-sizing: border-box; 
  border-bottom: 1px solid #555555;
}

.infomation_footer {
  padding: 16px 8% 40px 8%;
  box-sizing: border-box; 
  background-color: #202020;
}
.infomation_footer img {
  width: 7rem;
  margin: 20px 0 ;
}

.footer_info {
  font-family: NotoSansKR-Regular;
  color: #808080;
  font-size: 14px;
  line-height: 22px;
}



